*::before,
*::after {
  margin: 0;
  box-sizing: border-box;
  font-size: 18px;
}

.center-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

html {
  background-color: #f9f9f9;
  height: 100%;
  overflow-y: scroll
}

body {
  --sidebar-background-color: #fbf6f0;
  --content-background-color: #fff3d2;
  --task-background-color: #fbf6f0;
  --icon-button-size: 30px;

  display: grid;
  grid-template-columns: 400px 1fr;
  height: 100%;
}

.sidebar {
  --sidebar-gap: 16px;
  --user-icon-size: 56px;
  --title-icon-size: 36px;

  display: flex;
  flex-direction: column;
  gap: var(--sidebar-gap);

  height: 100%;
  background-color: var(--sidebar-background-color);
}

.sidebar-header-user {
  display: grid;
  grid-template-columns: var(--user-icon-size) 1fr var(--icon-button-size);
  align-items: center;
  gap: 6px;

  margin-top: calc(var(--sidebar-gap) * 1.5);
  padding: 0px 12px;
}

.user-icon {
  height: var(--user-icon-size);
  width: var(--user-icon-size);
  border-radius: 50%;
}

.username {
  font-size: 1.1rem;
  font-weight: bold;
}

button {
  display: flex;
  justify-content: center;
  align-items: center;

  background-color: var(--sidebar-background-color);
  border: none;
}

img.icon-button {
  height: var(--icon-button-size);
  width: var(--icon-button-size);
}

.shisa {
  display: flex;
  justify-content: center;
  align-items: center;
}

.project {
  display: grid;
  grid-template-columns: var(--title-icon-size) 1fr var(--icon-button-size);
  align-items: center;
  gap: 6px;

  margin-top: 6px;
  padding: 0px 12px;
}

.sidebar > hr {
  width: 96%;
  align-self: center;
}

.header-icon {
  height: var(--title-icon-size);
  width: var(--title-icon-size);
}

.sidebar h2 {
  font-size: 1.2rem;
}

.project-color {
  height: var(--icon-button-size);
  width: var(--icon-button-size);
  background-color: #F29877;
  border-radius: 50%;
  border: 2px solid black;
}

.content {
  --content-gap: 12px;
  --task-padding: 20px;
  --checkbox-size: 22px;
  --subtask-padding: 10px;
  --subtask-left-margin: 45px;

  background-color: var(--content-background-color);
  padding: 60px 60px;
}

.project-content > *,
.new-task-btn {
  margin: var(--content-gap) 0;
  white-space: pre-line;
}

.project-header {
  display: flex;
  align-items: center;
  gap: 12px;
}

.project-header > .project-color {
  --content-project-color-icon-size: 40px;
  height: var(--content-project-color-icon-size);
  width: var(--content-project-color-icon-size);
}

.project-header > h2 {
  font-size: 2rem;
}

.task {
  background-color: var(--task-background-color);
  border-radius: 8px;
  box-shadow: 3px 3px 5px -2px gray;

  padding: 18px 24px;
  margin: 22px 0;
}

.main-task {
  display: grid;
  grid-template-columns: var(--checkbox-size) 1fr repeat(3, var(--icon-button-size));
  align-items: center;
  gap: 12px;
}

.subtask {
  display: grid;
  grid-template-columns: var(--checkbox-size) 1fr var(--icon-button-size);
  align-items: end;
  gap: 12px;

  margin-left: var(--subtask-left-margin);
  padding: var(--subtask-padding) 0;
}

input[type="checkbox"] {
  height: var(--checkbox-size);
  width: var(--checkbox-size);
}

dialog {
  margin: auto;
  border-radius: 6px;
}

dialog > form {
  display: grid;
  grid-template-columns: fit-content(50px) 1fr;
  gap: 12px;
}

dialog > div {
  margin: 8px 0 24px 0;
  font-weight: 600;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;;;EAGE,SAAS;EACT,sBAAsB;EACtB,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,yBAAyB;EACzB,YAAY;EACZ;AACF;;AAEA;EACE,mCAAmC;EACnC,mCAAmC;EACnC,gCAAgC;EAChC,wBAAwB;;EAExB,aAAa;EACb,gCAAgC;EAChC,YAAY;AACd;;AAEA;EACE,mBAAmB;EACnB,sBAAsB;EACtB,uBAAuB;;EAEvB,aAAa;EACb,sBAAsB;EACtB,uBAAuB;;EAEvB,YAAY;EACZ,iDAAiD;AACnD;;AAEA;EACE,aAAa;EACb,wEAAwE;EACxE,mBAAmB;EACnB,QAAQ;;EAER,0CAA0C;EAC1C,iBAAiB;AACnB;;AAEA;EACE,6BAA6B;EAC7B,4BAA4B;EAC5B,kBAAkB;AACpB;;AAEA;EACE,iBAAiB;EACjB,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;;EAEnB,iDAAiD;EACjD,YAAY;AACd;;AAEA;EACE,+BAA+B;EAC/B,8BAA8B;AAChC;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,yEAAyE;EACzE,mBAAmB;EACnB,QAAQ;;EAER,eAAe;EACf,iBAAiB;AACnB;;AAEA;EACE,UAAU;EACV,kBAAkB;AACpB;;AAEA;EACE,8BAA8B;EAC9B,6BAA6B;AAC/B;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,+BAA+B;EAC/B,8BAA8B;EAC9B,yBAAyB;EACzB,kBAAkB;EAClB,uBAAuB;AACzB;;AAEA;EACE,mBAAmB;EACnB,oBAAoB;EACpB,qBAAqB;EACrB,uBAAuB;EACvB,2BAA2B;;EAE3B,iDAAiD;EACjD,kBAAkB;AACpB;;AAEA;;EAEE,4BAA4B;EAC5B,qBAAqB;AACvB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,uCAAuC;EACvC,8CAA8C;EAC9C,6CAA6C;AAC/C;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,8CAA8C;EAC9C,kBAAkB;EAClB,iCAAiC;;EAEjC,kBAAkB;EAClB,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,kFAAkF;EAClF,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,uEAAuE;EACvE,gBAAgB;EAChB,SAAS;;EAET,uCAAuC;EACvC,iCAAiC;AACnC;;AAEA;EACE,4BAA4B;EAC5B,2BAA2B;AAC7B;;AAEA;EACE,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,4CAA4C;EAC5C,SAAS;AACX;;AAEA;EACE,oBAAoB;EACpB,gBAAgB;AAClB","sourcesContent":["*,\n*::before,\n*::after {\n  margin: 0;\n  box-sizing: border-box;\n  font-size: 18px;\n}\n\n.center-content {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\nhtml {\n  background-color: #f9f9f9;\n  height: 100%;\n  overflow-y: scroll\n}\n\nbody {\n  --sidebar-background-color: #fbf6f0;\n  --content-background-color: #fff3